import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/autoplay';

// import required modules
import { Navigation, Pagination, Autoplay } from 'swiper/modules';

export default function Banner() {
  return (
    <div className="relative w-full h-[800px]">
      {/*    */}
      <div className="absolute inset-x-0 top-[20%] flex flex-col z-10 text-white text-center">
        <h1 className="text-6xl font-bold tracking-wide mt-[10px]">Immunology Laboratory</h1>
        <p className="text-3xl mt-[10px]">JBNU VET MEDICINE</p>
      </div>

      {/*   */}
      <Swiper
        className="w-full h-full"
        navigation={true} //  
        pagination={{ clickable: true }} //  
        autoplay={{ delay: 2000, disableOnInteraction: false }} //   
        modules={[Navigation, Pagination, Autoplay]} //   
      >
        <SwiperSlide>
          <img src="assets/banner1.png" alt="banner1" className="w-full h-full object-cover" />
        </SwiperSlide>
        <SwiperSlide>
          <img src="assets/banner2.png" alt="banner2" className="w-full h-full object-cover" />
        </SwiperSlide>
        <SwiperSlide>
          <img src="assets/banner3.png" alt="banner3" className="w-full h-full object-cover" />
        </SwiperSlide>
      </Swiper>
    </div>
  );
}
